<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data History Bet Bulan Berjalan</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="datas" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item) in items" :key='item["id"]'>
                                <!-- <td class="title">{{ index + 1 }}</td> -->
                                <td class="title">{{ item["Tanggal"] }}</td>
                                <td class="title">{{ item["Kode Barang"] }}</td>
                                <td class="title">{{ item["No LP"] }}</td>
                                <td class="title">{{ item["No Bet Induk"] }}</td>
                                <td class="title">{{ item["Keterangan"] }}</td>
                                <td class="title">{{ item["Masuk"] }}</td>
                                <td class="title">{{ item["Keluar"] }}</td>
                                <td class="title">{{ item["Penimbang"] }}</td>
                                <td class="title">{{ item["Penimbang"] }}</td>
                                <td class="title">{{ item["Tanggal Kadaluwarsa"] }}</td>
                                <!-- <td>
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click='deleteData(item["Kode Uji"])'>
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td> -->
                            </tr>
                        </tbody>
                    </template>
                    
                </v-data-table>
            </v-container>
        </v-card>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                statusUpdate: true,
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                dialog: false,
                keyword: '',
                headers: [
                    // {
                    //     text: 'No',
                    //     value: 'no',
                    //     class: 'title',
                    // },
                    {
                        text: 'Tanggal',
                        value: 'Tanggal',
                        class: 'title',
                    },
                    {
                        text: 'Barang',
                        value: 'Kode Barang',
                        class: 'title',
                    },
                    {
                        text: 'No LP',
                        value: 'No LP',
                        class: 'title',
                    },
                    {
                        text: 'Induk',
                        value: 'No Bet Induk',
                        class: 'title',
                    },
                    {
                        text: 'Ket',
                        value: 'Keterangan',
                        class: 'title',
                    },
                    {
                        text: 'Masuk',
                        value: 'Masuk',
                        class: 'title',
                    },
                    {
                        text: 'Keluar',
                        value: 'Keluar',
                        class: 'title',
                    },
                    {
                        text: 'Pet1',
                        value: 'Penimbang',
                        class: 'title',
                    },
                    {
                        text: 'Pet2',
                        value: 'Pemeriksa',
                        class: 'title',
                    },
                    {
                        text: 'Kadaluwarsa',
                        value: 'Tanggal Kadaluwarsa',
                        class: 'title',
                    },
                    // {
                    //     text: 'Aksi',
                    //     value: '',
                    //     class: 'title',
                    // },
                ],
                datas: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                form: {
                    keterangan: '',
                    kodeUji: '',
                },
                param: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'mutstokcpkb4tahuns'
                this.$http.get(uri).then(response => {
                    this.datas = response.data
                })
            },
            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>